import React from 'react';
import { CDBFooter,CDBBox, CDBFooterLink, CDBBtn, CDBIcon, CDBContainer } from 'cdbreact';
import { NavLink } from 'react-router-dom';

export const Footer = () => {

  const handle=()=> {
    //  using window.location.href 
    window.location.href = 'https://www.instagram.com/sufianartz/';
    return null;
  }
  return (
    <CDBFooter className="shadow">
      <CDBBox
        display="flex"
        justifyContent="between"
        alignItems="center"
        className="mx-auto py-4 flex-wrap"
        style={{ width: '80%' }}
      >
        <CDBBox display="flex" alignItems="center">
          <a href="/" className="d-flex align-items-center p-0 text-dark">
            <img src='https://upload.wikimedia.org/wikipedia/commons/1/1e/RPC-JP_Logo.png'
              alt="logo"
              width="30px"
            />
            <span className="ml-4 h5 mb-0 font-weight-bold">Sufianartz</span>
          </a>
          <small className="ml-2">&copy; SufianCo, 2022. All rights reserved.</small>
        </CDBBox>
        <CDBBox display="flex">
         <CDBBtn flat color="dark" className="p-2" onClick={handle}>
        {/* <NavLink to='/profile'> <CDBBtn flat color="dark" className="p-2" onClick={handle}> */}
        <CDBIcon fab icon="instagram" />
          </CDBBtn>
          {/* </NavLink> */}
        </CDBBox>
      </CDBBox>
    </CDBFooter>
  );
};