import React,{useState,useEffect} from 'react'
import axios from 'axios';
import CreateIcon from '@mui/icons-material/Create';
import DeleteIcon from '@mui/icons-material/Delete';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import EmailIcon from '@mui/icons-material/Email';
import WorkIcon from '@mui/icons-material/Work';
import SmartphoneIcon from '@mui/icons-material/Smartphone';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { NavLink, useNavigate, useParams } from 'react-router-dom';

const Detail = () => {

    const [getuserdata, setUserdata] = useState([]);
console.log(getuserdata);

const   {id}=  useParams("")
console.log (id);

const navigate= useNavigate();

    const getdata = async () => {

        const res = await axios.get(`http://localhost:3000/getuser/${id}`)

        const data = await res.data;
        console.log(data);

        if (res.status === 422 || !data) {
            console.log("error");

        } else {
            setUserdata(data)
            console.log("get data");

        }
    }
    useEffect(() => {
        getdata();
    }, [])

    const deleteuser = async (id) => {

        const res2 = await fetch(`/deleteuser/${id}`, {
            method: "DELETE",
            headers: {
                "Content-Type": "application/json"
            }
        });

        const deletedata = await res2.json();
        console.log(deletedata);

        if (res2.status === 422 || !deletedata) {
            console.log("error");
            
        } else {
            alert("user deleted");
            navigate('/');
           
        }

    }

    return (
        <div className="container mt-3">
            <h1 style={{ fontWeight: 400 }}>Welcome {getuserdata.name}</h1>
            <Card sx={{ maxWidth: 600 }}>
                <CardContent >
                    <div className="add_btn">
                        <NavLink to={`/edit/${getuserdata._id}`}> <button className='btn btn-primary mx-2'><CreateIcon /></button></NavLink>  
                        <button className='btn btn-danger' onClick={() => deleteuser(getuserdata._id)}><DeleteIcon /></button>
                    </div>
                    <div className="row">
                        <div className="left_view col-lg-6 col-md-6 col-12">
                            <img src='https://static.vecteezy.com/system/resources/thumbnails/005/544/770/small/profile-icon-design-free-vector.jpg' style={{ width: 50 }} alt='profile' />
                            <h3 className='mt-3'>Name: <span style={{ fontWeight: 400 }}>{getuserdata.name}</span></h3>
                            <h3 className='mt-3'>Age: <span style={{ fontWeight: 400 }}>{getuserdata.age}</span></h3>
                            <p className='mt-3'><EmailIcon />Email: <span>{getuserdata.email}</span></p>
                            <p className='mt-3'><WorkIcon />Occupation: <span>{getuserdata.work}</span></p>
                        </div>
                        <div className="right_view col-lg-6 col-md-6 col-12">

                            <p className='mt-5'><SmartphoneIcon />Phone: <span>{getuserdata.no}</span></p>
                            <p className='mt-3'><LocationOnIcon />Address: <span>{getuserdata.addrs}</span></p>
                            <p className='mt-3'>Description: <span>{getuserdata.desc}</span></p>

                        </div>
                    </div>



                </CardContent>



            </Card>

        </div>
    )
}

export default Detail