import React, { useState, useEffect } from 'react'
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import CreateIcon from '@mui/icons-material/Create';
import DeleteIcon from '@mui/icons-material/Delete';

import axios from 'axios';
import { NavLink } from 'react-router-dom';

const Home = () => {

    const [getuserdata, setUserdata] = useState([]);
    console.log(getuserdata)
    

    const getdata = async (e) => {


        const res = await axios.get('http://localhost:8003/getdata')



        const data = await res.data;
        console.log(data);

        if (res.status === 422 || !data) {
            console.log("error");

        } else {
            setUserdata(data)
            console.log("get data");

        }
    }
    useEffect(() => {
        getdata();
    }, [])

    const deleteuser = async (id) => {

        const res2 = await fetch(`/deleteuser/${id}`, {
            method: "DELETE",
            headers: {
                "Content-Type": "application/json"
            }
        });

        const deletedata = await res2.json();
        console.log(deletedata);

        if (res2.status === 422 || !deletedata) {
            console.log("error");
        } else {
            console.log("user deleted");
            getdata();
        }

    }


    return (
        <div className="mt-5">
            <div className="container">
                <div className='add_btn mt-2 mb-2'>
                <NavLink to="/register" > <button className='btn btn-primary'>Add data</button></NavLink> 

                </div>
                <table class="table">
                    <thead>
                        <tr className='table-dark'>
                            <th scope="col">id</th>
                            <th scope="col">User</th>
                            <th scope="col">Age</th>
                            <th scope="col">Email</th>
                            <th scope="col">Job</th>
                            <th scope="col">Number</th>
            
                            <th scope="col"></th>
                        </tr>
                    </thead>
                    <tbody>

                        {
                            getuserdata.map((element, id) => {
                                return (
                                    <>


                                        <tr>
                                    <th scope="row">    {id}</th>
                                            <td>    {element.name}</td>
                                            <td>    {element.age}</td>
                                            <td>{element.email}</td>
                                            <td>{element.work}</td>
                                            <td>{element.no}</td>
                                            <td className='d-flex justify-content-between'>
                                                <NavLink to={`view/${element._id}`}><button className='btn btn-success'><RemoveRedEyeIcon /></button></NavLink>
                                                <NavLink to={`edit/${element._id}`}><button className='btn btn-primary'><CreateIcon /></button></NavLink>
                                                <button className='btn btn-danger' onClick={() => deleteuser(element._id)}><DeleteIcon /></button>
                                            </td>
                                        </tr>


                                    </>
                                )

                            })
                        }





                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default Home