import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { useParams } from 'react-router-dom';



const Edit = () => {
    // const [getuserdata, setUserdata] = useState([]);
    // console.log(getuserdata);

    const [inpVal, setINP] = useState({
        name: '',
        email: '',
        age: '',
        no: '',
        work: '',
        addrs: '',
        desc: ''

    })

    const setdata = (e) => {
        console.log(e.target.value);
        const { name, value } = e.target;
        setINP((preval) => {
            return {
                ...preval,
                [name]: value
            }
        })
    }

    const { id } = useParams("")
    console.log(id);


    const getdata = async () => {

        const res = await axios.get(`/getuser/${id}`)

        const data = await res.data;
        console.log(data);

        if (res.status === 422 || !data) {
            console.log("error");

        } else {
            setINP(data)
            console.log("get data");

        }
    }
    useEffect(() => {
        getdata();
    }, [])

    const updateuser = async(e) => {
        e.preventDefault();
    
        const { name, email, work, addrs, no, desc, age } = inpVal;


    const res2 = await fetch(`/updateuser/${id}`, {
        method: "PATCH",
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify({ name, email, work, addrs, no, desc, age })
    });
    const data2 = await res2.json();
    console.log(data2);

    if (res2.status === 422 || !data2) {

        alert("Fill the data")
    }
    else
        alert("data updated")
}
// const res2 = await axios.get(`/updateuser/${id}`)

// const data2 = await res2.data;
// console.log(data2);

// if (res2.status === 422 || !data2) {
//     console.log("fill data");

// } else {
//     setINP(data2)
//     console.log("data added");

// }
// }
    return (
        <div className='container'>
            <form>
                <div className="row">
                    <div className="mb-3 col-lg-6 col-mg-6 col-12">
                        <label for="exampleInputEmail1" className="form-label">Name</label>
                        <input type="text" onChange={setdata} value={inpVal.name} name='name' className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                    </div>
                    <div className="mb-3 col-lg-6 col-mg-6 col-12">
                        <label for="exampleInputPassword1" className="form-label">Email</label>
                        <input type="email" value={inpVal.email} onChange={setdata} name='email' className="form-control" id="exampleInputPassword1" />
                    </div>
                    <div className="mb-3 col-lg-6 col-mg-6 col-12">
                        <label for="exampleInputPassword1" className="form-label">Age</label>
                        <input type="text" value={inpVal.age} onChange={setdata} name='age' className="form-control" id="exampleInputPassword1" />
                    </div>
                    <div className="mb-3 col-lg-6 col-mg-6 col-12">
                        <label for="exampleInputPassword1" className="form-label">Mobile</label>
                        <input type="tel" value={inpVal.no} onChange={setdata} name='no' className="form-control" id="exampleInputPassword1" />
                    </div>
                    <div className="mb-3 col-lg-6 col-mg-6 col-12">
                        <label for="exampleInputPassword1" className="form-label">Work</label>
                        <input type="text" value={inpVal.work} onChange={setdata} name='work' className="form-control" id="exampleInputPassword1" />
                    </div>
                    <div className="mb-3 col-lg-6 col-mg-6 col-12">
                        <label for="exampleInputPassword1" className="form-label">Address</label>
                        <input type="text" value={inpVal.addrs} onChange={setdata} name='addrs' className="form-control" id="exampleInputPassword1" />
                    </div>
                    <div className="mb-3 col-lg-12 col-mg-12 col-12">
                        <label for="exampleInputPassword1" className="form-label">Description</label>
                        <textarea name='desc' value={inpVal.desc} onChange={setdata} className="form-control" id="exampleInputPassword1" />
                    </div>

                    <button type="submit" onClick={updateuser} className="btn btn-primary">Submit</button>
                </div>
            </form>
        </div>
    )
}

export default Edit






